import React from 'react'

//     hover 
export default function SelectThemeActive(selectedTheme, theme, imgLocation) {    //:  useState  , theme 
    //  white  
    let result = {
      backgroundPosition: "center", 
      backgroundRepeat: "no-repeat", 
      backgroundImage: ` linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${process.env.PUBLIC_URL+imgLocation})`,
      color: "white", 
      paddingTop:"20px",
      marginBottom:"5px", 
      marginRight: "5px", 
      marginLeft:"5px" ,
      opacity:"0.85",
      borderRadius: "10px",
      top:"10%",
      position:"relative", 
      float: "left", 
      width: "30%", 
      height:"50px", 
      textAlign: "center"};

    //    : hover  ,  
    if (selectedTheme === theme) {
      result.backgroundImage= ` linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(${process.env.PUBLIC_URL+imgLocation})`
    }
    return result;
}
